.logoanimated--building {
  -webkit-animation: logoAni 3s infinite;
          animation: logoAni 3s infinite;
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;
}

.logoanimated--bolt {
  -webkit-transform-origin: bottom;
      -ms-transform-origin: bottom;
          transform-origin: bottom;
  -webkit-transform: translateY(66px) translateX(18px) scale(0.75);
      -ms-transform: translateY(66px) translateX(18px) scale(0.75);
          transform: translateY(66px) translateX(18px) scale(0.75);

  -webkit-animation: boltAni 3s infinite;

          animation: boltAni 3s infinite;
  -webkit-animation-delay: 2.2s;
          animation-delay: 2.2s;
}

@-webkit-keyframes logoAni {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  45% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
  }
  65% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@keyframes logoAni {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  45% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
  }
  65% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@-webkit-keyframes boltAni {
  0% {
    -webkit-transform: translateY(56px) translateX(18px) scale(0.75);
            transform: translateY(56px) translateX(18px) scale(0.75);
    opacity: 1;
  }
  45% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
    opacity: 1;
  }
  65% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
  }
  70% {
    opacity: 0;
  }
  95% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(56px) translateX(18px) scale(0.5);
            transform: translateY(56px) translateX(18px) scale(0.5);
    opacity: 0;
  }
}

@keyframes boltAni {
  0% {
    -webkit-transform: translateY(56px) translateX(18px) scale(0.75);
            transform: translateY(56px) translateX(18px) scale(0.75);
    opacity: 1;
  }
  45% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
    opacity: 1;
  }
  65% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
  }
  70% {
    opacity: 0;
  }
  95% {
    -webkit-transform: scaleY(0.8);
            transform: scaleY(0.8);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(56px) translateX(18px) scale(0.5);
            transform: translateY(56px) translateX(18px) scale(0.5);
    opacity: 0;
  }
}



